@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap");
header {
  background-color: var(--primary-color);
  height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
header ul {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 85vw;
  height: 100%;
  flex-direction: row;
}
header ul li {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  color: rgba(var(--text-color-gray), 0.5);
  width: 10rem;
}
header ul li:hover {
  color: var(--text-color-light);
  background-color: var(--secondary-color);
}

@media (max-width: 570px) {
  header ul {
    width: 100%;
  }
}
.hero__section {
  width: 100%;
  height: auto;
}
.hero__section .title__side {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 25vh;
  width: 100%;
}
.hero__section .title__side h2 {
  font-size: 5rem;
}
.hero__section .title__side h2 strong {
  font-weight: bolder;
  color: var(--text-color-dark);
}
.hero__section .banner__holder {
  width: 100%;
  height: 50vw;
  max-height: 65vh;
  background-size: cover;
  background-image: url("/res/slider1.jpg");
}

@media (max-width: 570px) {
  .hero__section .title__side h2 {
    font-size: clamp(2rem, 10vw, 8rem);
  }
}
.card__section {
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  width: 100%;
  display: inline-grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 0.5rem;
  justify-items: center;
  align-items: center;
  justify-content: center;
  align-content: center;
}
.card__section :nth-child(1) .img__holder {
  background-image: url(/res/pic1.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.card__section :nth-child(2) .img__holder {
  background-image: url(/res/pic2.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.card__section :nth-child(3) .img__holder {
  background-image: url(/res/pic3.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.card__section .card {
  transition: 0.1s cubic-bezier(0.2, 0.81, 0.93, 0.77);
  width: 20rem;
  max-width: 20rem;
}
.card__section .card:hover {
  transition: 0.1s cubic-bezier(0.2, 0.81, 0.93, 0.77);
  box-shadow: 0px 10px 15px -5px rgba(0, 0, 0, 0.4);
}
.card__section .card .subtitle__holder {
  height: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--secondary-color);
}
.card__section .card .subtitle__holder h5 {
  color: var(--text-color-light);
}
.card__section .card .img__holder {
  height: 20vh;
}
.card__section .card .paragraph__holder {
  padding: 1rem;
  height: auto;
  background-color: var(--tertiary-color);
}
.card__section .card .paragraph__holder p {
  color: var(--text-color-light);
  line-height: 1.5rem;
}
.card__section .card .btn__container {
  background-color: var(--tertiary-color);
  height: auto;
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}
.card__section .card .btn__container i {
  height: 4rem;
  width: 4rem;
  transition: 0.2s cubic-bezier(0.55, 0.085, 0.68, 0.53);
  background-image: url(/res/plus_btn.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.card__section .card .btn__container i:hover {
  transition: 0.2s cubic-bezier(0.55, 0.085, 0.68, 0.53);
  background-image: url(/res/plus_btn_h.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

@media (max-width: 1080px) {
  .card__section {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
  }
}
@media (max-width: 675px) {
  .card__section {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr;
  }
}
.more__section {
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  width: 100%;
  display: inline-grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 1rem;
  justify-items: center;
  justify-content: center;
  align-content: center;
}
.more__section .more__card {
  margin-top: 0.5rem;
  width: 20rem;
  height: fit-content;
}
.more__section .more__card .title__holder {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  height: 4rem;
}
.more__section .more__card .title__holder h4 {
  font-family: "Roboto";
  color: var(--text-color-gray);
}
.more__section .more__card .paragraph__holder {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  height: 6rem;
}
.more__section .more__card .paragraph__holder div {
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  margin-right: 0.5rem;
}
.more__section .more__card .paragraph__holder p {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: black;
}
.more__section .more__card .subtitle {
  font-weight: bold;
  margin-bottom: 0.75rem;
  color: var(--text-color-dark);
}
.more__section .more__card button {
  margin-top: 0.75rem;
  background-color: var(--tertiary-color);
  width: 7.5rem;
  height: 3rem;
  border: none;
  font-size: large;
  color: var(--text-color-light);
  transition: 0.1s cubic-bezier(0.2, 0.81, 0.93, 0.77);
}
.more__section .more__card button:hover {
  transition: 0.1s cubic-bezier(0.2, 0.81, 0.93, 0.77);
  box-shadow: 0px 7px 15px -5px rgba(0, 0, 0, 0.4);
}

@media (max-width: 1080px) {
  .more__section {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
  }
}
@media (max-width: 675px) {
  .more__section {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr;
  }
}
footer {
  border-top: solid 1px var(--tertiary-color);
  background-color: var(--primary-color);
  display: flex;
  align-items: flex-end;
  height: 10vh;
}
footer .subdiv {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}
footer .subdiv h5 {
  margin-left: 1rem;
  width: auto;
  margin-bottom: 1rem;
}
footer .subdiv .icon__container {
  margin-bottom: 1rem;
  margin-right: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
footer .subdiv .icon__container i {
  display: block;
  background-image: url("/res/reseaux.png");
  width: 32px;
  height: 32px;
  margin: 0 0.25rem 0 0.25rem;
}
footer .subdiv .icon__container i:nth-child(1) {
  background-position: 0 0;
}
footer .subdiv .icon__container i:nth-child(2) {
  background-position: 64px 0;
}
footer .subdiv .icon__container i:nth-child(3) {
  background-position: 32px 0;
}

:root {
  --primary-color: #FFFFFF;
  --secondary-color: #662f32;
  --tertiary-color: #645955;
  --text-color: #000000;
  --text-color-light: #f5f5f5;
  --text-color-dark: #662f32;
  --text-color-gray: #272727;
}

* {
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--tertiary-color);
}

li {
  list-style-type: none;
}

section {
  background-color: var(--primary-color);
}

main {
  width: 60%;
  min-width: 72rem;
}

@media (max-width: 1153px) {
  main {
    width: 100%;
    min-width: auto;
  }
}
p, button, li, h4, h5 {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: black;
}